.website {
    display:flex;
    justify-content:center;
    align-items:center;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease;
    background:rgb(29, 29, 29);
    overflow-y:auto !important;
    overflow-x:hidden;
    height:100vh;
}

.website .ww {
    width:1338px;
    display:none;
    animation-duration:0.7s;
    animation-fill-mode:both;
    animation-timing-function:ease-out;
}

.website .ww a {
    display:flex;
    align-items:center;
    width:max-content;
    font-size:25px;
    color:rgb(172, 172, 172);
    text-decoration:none;
    transition:0.2s color;
}

.website .ww a:hover {
    color:white;
}

.website .ww a span {
    margin-left:10px;
}

.website .content {
    overflow-y:hidden;
    overflow-x:auto;
}

.website .content::-webkit-scrollbar {
    display:none;
}

.website .contentWrap {
    margin-top:70px;
    display:flex;
    justify-content:center;
    width:3900px;
}

.website .contentSub {
    padding-bottom:10px;
    display:flex;
    justify-content:space-between;
    width:3100px;
    height:80vh;
}

.website .sontentSubWrap {
    width:500px;
    height:100%;
}

.website .contentInside {
    display:flex;
    justify-content:center;
    align-items:center;
    width:450px;
    height:100%;
    border:1px solid #afafaf;
    box-sizing:border-box;
    background:white;
    transition:transform 0.1s ease, opacity 0.2s ease;
}

.website .contentInside:not(.website .contentInside:first-child) {
    opacity:0.2;
}

.website .insideWrap  {
    display:flex;
    flex-direction:column;
    align-items:center;
    width:95%;
    height:95%;
}

.website .insideWrap img {
    width:100%;
    height:300px;
    border:1px solid #111;
    box-sizing:border-box;
}

.website .contentInside h4 {
    font-size:35px;
    margin-bottom:30px;
}

.website .contentInside p {
    text-align:center;
    height:100px;
    font-size:14px;
    white-space:pre-line;
}

.website .contentInside ul {
    display:flex;
    flex-direction:row;
}

.website .contentInside ul li {
    margin:0px 5px 50px 5px;
    width:35px;
    height:35px;
    border-radius:50%;
    border:1px solid #111;
    box-sizing:border-box;
}

.website .contentInside a {
    padding-block:5px;
    padding-inline:30px;
    margin-bottom:20px;
    color:rgb(131, 207, 128);
    border:2px solid rgb(184, 184, 184);
    border-radius:30px;
    transition:0.2s;
    box-sizing:border-box;
}

.website .contentInside fieldset {
    width:inherit;
    display:grid;
    grid-template-columns:repeat(4, 1fr);
    grid-row-gap:10px;
}

.website .contentInside fieldset div {
    display:flex;
    justify-content:space-between;
}

.website .contentInside fieldset legend {
    text-align:center;
}

.website .contentInside fieldset span:first-child {
    margin:0 auto;
    font-weight:bold;
}

.website .contentInside fieldset div:last-child span:last-child {
    display:none;
}

.react {color:#63d1f5;}
.typescript {color:#2f74c0;}
.javascript {color:#efd81d;}
.express {color:#353535;}
.mariadb {color:#0c1d44;}
.gcp {
    background:linear-gradient(to right, #4183f1 40%, #e40200 40%, #f7b303);
    background-clip:content-box;
    -webkit-background-clip:text;
    color:transparent;
}
.docker {color:#0396c0;}

@media (min-height:1500px) {
    .website .contentSub {
        height:1200px;
    }
}